html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-size: 16px;
  line-height: 1.4;
  color: #252525;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
  min-width: 320px; }
  body input,
  body textarea,
  body button {
    outline: none; }
    body input:focus,
    body textarea:focus,
    body button:focus {
      outline: none; }

.site {
  overflow-x: hidden; }

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  min-width: 1180px; }

.col {
  padding-left: 15px;
  padding-right: 15px; }

header {
  margin-bottom: 30px; }

.header-content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-top: 20px; }
  .header-content__left {
    max-width: 240px;
    width: 100%; }
  .header-content__right {
    max-width: calc(100% - 240px);
    width: 100%; }

.logo {
  display: block;
  text-decoration: none;
  color: #252525; }
  .logo:hover .logo-img {
    background-size: 110%; }
  .logo-img {
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 1;
    overflow: hidden;
    transition: all .25s ease; }
    .logo-img::before {
      content: '';
      padding-top: 53%;
      display: block; }
  .logo span {
    display: block;
    text-align: center;
    font-size: 12px;
    letter-spacing: .05px;
    text-transform: uppercase;
    line-height: 1;
    padding-top: 10px;
    font-weight: bold; }

.top-line {
  display: flex;
  justify-content: space-between;
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: 24px;
  padding-bottom: 5px;
  position: relative; }
  .top-line::before {
    content: '';
    width: calc(100% - 30px);
    height: 1px;
    left: 15px;
    bottom: -5px;
    position: absolute;
    background-color: #D7D7D7; }
  .top-line__menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    margin-left: -10px;
    margin-right: -10px; }
    .top-line__menu li {
      line-height: 1;
      padding-left: 10px;
      padding-right: 10px; }
      .top-line__menu li a {
        text-decoration: none;
        color: #252525;
        font-size: 12px;
        line-height: 1;
        display: block; }
        .top-line__menu li a:hover {
          text-decoration: underline; }

.auth {
  display: flex; }
  .auth-item--border {
    border-right: 1px solid #252525;
    padding-right: 15px;
    margin-right: 15px; }
  .auth-item a {
    text-decoration: none;
    color: #252525;
    font-size: 12px;
    line-height: 1;
    display: block; }
    .auth-item a:hover {
      text-decoration: underline; }

.header-group {
  display: flex;
  justify-content: space-between;
  margin-left: -15px;
  margin-right: -15px; }

.banners {
  background-color: #D7D7D7;
  display: block;
  overflow: hidden; }
  .banners--header {
    width: 253px;
    height: 70px; }

.site-options {
  display: flex;
  flex-direction: column;
  margin-bottom: -13px; }
  .site-options:hover .site-options__link {
    opacity: 0.6; }
    .site-options:hover .site-options__link:hover {
      opacity: 1; }
  .site-options__link {
    display: block;
    padding-left: 30px;
    text-decoration: underline;
    font-size: 14px;
    line-height: 1;
    margin-bottom: 13px;
    color: #252525;
    position: relative; }
    .site-options__link:hover {
      text-decoration: none; }
    .site-options__link::before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%); }
  .site-options .basket-link::before {
    background-image: url("../img/icons/cart.png");
    width: 19px;
    height: 18px;
    background-size: cover;
    left: 0; }
  .site-options .favorit-link::before {
    background-image: url("../img/icons/star.png");
    width: 15px;
    height: 15px;
    background-size: cover;
    left: 5px; }
  .site-options .compare-link::before {
    background-image: url("../img/icons/balance.png");
    width: 18px;
    height: 16px;
    background-size: cover;
    left: 5px; }

.header-product {
  display: flex;
  margin-left: -6px;
  margin-right: -6px; }
  .header-product__row {
    padding-left: 6px;
    padding-right: 6px; }
  .header-product__item {
    display: block;
    width: 72px;
    border: 1px solid #D7D7D7;
    background-size: 51px;
    background-repeat: no-repeat;
    background-position: center;
    transition: all .25s ease; }
    .header-product__item:hover {
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2); }
    .header-product__item::before {
      content: '';
      padding-top: 100%;
      display: block; }

.header-contacts__time {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  line-height: 1.2;
  margin-bottom: 15px; }

.header-phone {
  font-size: 25px;
  font-weight: bold;
  line-height: 1;
  text-decoration: none;
  text-align: center;
  color: #252525;
  margin-bottom: 17px;
  display: block; }
  .header-phone:hover {
    text-decoration: underline; }

.header-city {
  border-top: 1px solid #D7D7D7;
  padding-top: 6px;
  display: flex;
  justify-content: space-between; }
  .header-city span {
    font-size: 12px; }
  .header-city a {
    font-size: 12px;
    position: relative;
    color: #252525;
    text-decoration: none;
    border-bottom: 1px dashed #252525; }
    .header-city a:hover {
      border-bottom-color: transparent; }
    .header-city a::before {
      content: '';
      border: 4px solid transparent;
      border-left: 4px solid #252525;
      position: absolute;
      left: -7px;
      margin-top: 1px;
      top: 50%;
      transform: translateY(-50%); }

.nav-top ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  border-radius: 11px;
  overflow: hidden;
  z-index: 1;
  box-shadow: 0 2px 3px 2px rgba(0, 0, 0, 0.25);
  display: flex;
  position: relative; }
  .nav-top ul::before {
    /*убрал фризы у border-radius*/
    content: '';
    width: calc(100% - 30px);
    height: 100%;
    position: absolute;
    left: 15px;
    top: 0;
    background-color: #252525;
    z-index: -1; }
  .nav-top ul li:first-child {
    margin-right: auto; }
  .nav-top ul li:last-child {
    margin-left: auto; }
  .nav-top ul li a {
    display: block;
    padding: 10px 17px;
    color: #ffffff;
    text-decoration: none;
    font-weight: bold;
    font-size: 14px;
    text-decoration: none;
    line-height: 1; }
    .nav-top ul li a.active {
      background-color: #FFCC00;
      color: #252525; }
      .nav-top ul li a.active:hover {
        text-decoration: none; }
    .nav-top ul li a:hover {
      text-decoration: underline; }
  .nav-top ul li .nav-search {
    width: 40px;
    height: 34px;
    background-color: #FFCC00;
    display: block;
    background-image: url("../img/icons/search.png");
    background-repeat: no-repeat;
    background-position: center; }
  .nav-top ul li .nav-sael {
    background-color: #FFCC00;
